.navbar {
  font-size: 1.4rem;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  padding-top: 5rem;
  padding-bottom: 1.7rem;
  transition: transform 1s;
}
.navbar__logo a {
  font-size: 1.6rem;
  display: flex;
}
.navbar__logo p {
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 0.652rem;
}
.navbar__menu {
  font-weight: 400;
  display: flex;
}
.navbar__menu.openMenuMobile {
  background: rgba(19, 19, 19, 0.8);
  transform: translateY(0);
  opacity: 1;
  transition: all 0.5s ease;
}
.navbar__menu li {
  margin: 1rem;
}
.navbar__menu li a {
  color: #fff;
  text-transform: uppercase;
}
.navbar__location {
  display: none;
  font-weight: 400;
}
.navbar__location li a {
  color: #fff;
  text-transform: uppercase;
}
.navbar__hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
}
.navbar__hamburger span {
  display: block;
  background: #fff;
  width: 2rem;
  height: 0.265rem;
  margin-top: 0.265rem;
  margin-bottom: 0.265rem;
  transition: all 250ms ease;
}
.navbar__hamburger.active span:nth-child(1) {
  margin: 8px auto;
  transform: translateY(13px) rotate(45deg);
}
.navbar__hamburger.active span:nth-child(2) {
  opacity: 0;
}
.navbar__hamburger.active span:nth-child(3) {
  margin: 8px auto;
  transform: translateY(-13px) rotate(-45deg);
}